<template>
    <div id='Slider'>
        <div id="myCarousel" class="carousel slide" data-ride="carousel">
            <!-- Indicators -->
            <ol class="carousel-indicators">
                <li data-target="#myCarousel" data-slide-to="0" class="active"></li>
                <li data-target="#myCarousel" data-slide-to="1"></li>
                <li data-target="#myCarousel" data-slide-to="2"></li>
            </ol>

            <!-- Wrapper for slides -->
            <div class="carousel-inner">
                <div class="item active">
                    <img src="https://www.medicalcarellc.com/wp-content/uploads/Ortho-WEB-Slider2.png" alt="Chania">
                </div>

                <div class="item">
                    <img src="https://www.medicalcarellc.com/wp-content/uploads/2-Patient-Forms.png" alt="Chicago">
                </div>

                <div class="item">
                    <img src="https://www.medicalcarellc.com/wp-content/uploads/4-Wellness-Visit.png" alt="New York">
                </div>
            </div>

            <!-- Left and right controls -->
            <a class="left carousel-control" href="#myCarousel" data-slide="prev">
                <span class="glyphicon glyphicon-chevron-left"></span>
                <span class="sr-only">Previous</span>
            </a>
            <a class="right carousel-control" href="#myCarousel" data-slide="next">
                <span class="glyphicon glyphicon-chevron-right"></span>
                <span class="sr-only">Next</span>
            </a>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Slider',
        data () {
            return {}
        }
    }
</script>

<style scoped>
    #Slider{
        max-width:980px;
        max-height:445px;
        margin-right:auto;
        margin-left:auto;
    }
    .carousel-caption{
        font-size:18pt;
        font-weight:bold;
        color:white;
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: black;
    }
    .carousel-caption h3{
        font-size:30pt;
        font-weight:bold;
        color:white;
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: black;
    }
</style>


